@font-face {
  font-weight: 500;
  font-family: "Poppins";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Poppins";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/cormorant-garamond-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Cormorant Garamond";
  font-style: italic;
  font-display: swap;
  src: url("../fonts/cormorant-garamond-bold-italic.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold-italic.woff") format("woff"); }

html {
  scroll-behavior: smooth;
  height: 100%; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  color: #1d1613;
  background-color: #ffffff;
  height: 100%; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  background-position: 0 0; }

.main-page__wrapper {
  margin-top: 30px;
  padding: 0 20px; }

.main-page__wrapper h1 {
  margin: 0; }

.main-page__add-task {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 30px; }

.main-page__add-task input {
  border: 1px solid #000000;
  cursor: pointer; }
  .main-page__add-task input:hover {
    background-color: #ffc0cb; }

.main-page__add-task button {
  margin-left: 15px; }

.main-page__new-task-btn {
  position: relative;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border: 1px solid #000000;
  cursor: pointer; }
  .main-page__new-task-btn::after, .main-page__new-task-btn::before {
    position: absolute;
    content: "";
    top: 8px;
    left: 4px;
    width: 10px;
    height: 2px;
    background-color: #000000; }
  .main-page__new-task-btn::after {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg); }
  .main-page__new-task-btn:hover {
    background-color: #ffc0cb; }
    .main-page__new-task-btn:hover::after, .main-page__new-task-btn:hover::before {
      background-color: #292929; }

.main-page__clear-task-btn {
  width: 100px;
  height: 20px;
  border: 1px solid #000000;
  background-color: #ffffff;
  cursor: pointer; }
  .main-page__clear-task-btn:hover {
    background-color: #ffc0cb; }

.main-page__list {
  margin: 0;
  padding: 0;
  margin-top: 20px; }

.list {
  list-style: none; }

.list li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 10px; }

.list li:first-child {
  margin-top: 0; }

.list p {
  margin: 0;
  margin-left: 5px; }

.list__check-wrapper span {
  margin-left: 15px; }

.list button {
  position: relative;
  width: auto;
  height: 20px;
  background-color: #ffffff;
  border: 1px solid #000000;
  cursor: pointer; }
  .list button:hover {
    background-color: #ffc0cb; }
    .list button:hover::after, .list button:hover::before {
      background-color: #292929; }
